.body {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin: auto;
  background-color:lightsteelblue;
  
}

.main{
    width: 70%;
    height:95%;
    background-color:rgba(236, 202, 172, 0.788);
    margin: auto;
    position: fixed;
    top: 0%;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 1em;
    overflow:scroll;   
    box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
   
}

.main::-webkit-scrollbar {
    display: none;

  }



.profilepic{
    width:30%;
    border-radius: 50%;
    float: left;
    margin-right:3em;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2), 0 1.5px 5px 0 rgba(0,0,0,0.19);
   
}

.name{
    margin-top: 5px;
}


.grid{
    display: grid;
    grid-template-columns: auto auto auto;
}

.gridcell{
    border: 1px solid rgb(83, 82, 82);
    margin-right:1em ;
    padding: 1em;
    box-sizing: border-box;
    background-color: rgba(201, 168, 139, 0.466);
    
   
}

.gridcell:hover{
    border: 1px solid black;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}

.gridcell a{
 
 border: 1px solid black;
 padding: 0.5em 2em;
 text-decoration: none;
 font-weight: bold;
 border-radius: 12px;
 color: black;
 display: block;
text-align: center;

background-color: lightgrey;

}



.gridcell h3{
 background-color: white;
 padding-left: 5px;
border-radius: 0px 10px 10px 0px;
}

/* resume buttons */
.header a{
    border: 1px solid black;
    padding: 0.5em 0.5em;
    text-decoration: none;
    font-weight: bold;
    border-radius: 12px;
    color: black;
    margin-right:5px;
   
   
   background-color: lightgrey;
}

a:hover{
    background-color: rgba(211, 211, 211, 0.466);
    color: white;
  
}
